const initState = {
    indexImageClick: 0,
    indexWordAdjustClick: 0,
    indexDWordAdjustClick: 0
}
export const backgroundReducer = (state = initState, action) => {
    switch (action.type) {
        case 'BACKGROUND_CLICK':
            if (state.indexImageClick > 0) {
                return {
                    ...state,
                    indexImageClick: state.indexImageClick - 1
                };
            }
            if (state.indexWordAdjustClick > 0) {
                return {
                    ...state,
                    indexWordAdjustClick: state.indexWordAdjustClick - 1
                };
            }
            if (state.indexDWordAdjustClick > 0) {
                return {
                    ...state,
                    indexDWordAdjustClick: state.indexDWordAdjustClick - 1
                };
            }
            return state
        case 'BACKGROUND_IMAGE_CLICK':
            return {
                ...state,
                indexImageClick: 2
            };
        case 'BACKGROUND_WORDADJUST_CLICK':
            return {
                ...state,
                indexWordAdjustClick: 2
            };
        case 'BACKGROUND_DWORDADJUST_CLICK':
            return {
                ...state,
                indexDWordAdjustClick: 2
            };

        default:
            return state
    }
}
