const InitialState = {
    addWordDisplay: {
        top: '100',
        left: '100',
        width: '100',
        fontSize: '14',
        tag: '1',
        text: '1234'
    },
    editWordDisplay: {
        top: '',
        left: '',
        width: '',
        fontSize: '',
        tag: '',
        text: '',
        id: '',
    }
};
export const wordDisplayReducer = (state = InitialState, action) => {
    switch (action.type) {
        case 'TOGGLE_WORDDISPLAY':
            return {
                ...state,
                editWordDisplay: {
                    ...state.editWordDisplay,
                    left: action.wordDisplay.left,
                    top: action.wordDisplay.top,
                    width: action.wordDisplay.width,
                    fontSize: action.wordDisplay.fontSize,
                    tag: action.wordDisplay.tag,
                    text: action.wordDisplay.text,
                    id: action.wordDisplay.id,
                }
            };
        case 'CHANGE_ADD_WORDDISPLAY_LEFT':
            return {
                ...state,
                addWordDisplay: {
                    ...state.addWordDisplay,
                    left: action.left,
                }
            };
        case 'CHANGE_ADD_WORDDISPLAY_TOP':
            return {
                ...state,
                addWordDisplay: {
                    ...state.addWordDisplay,
                    top: action.top,
                }
            };
        case 'CHANGE_ADD_WORDDISPLAY_TAG':
            return {
                ...state,
                addWordDisplay: {
                    ...state.addWordDisplay,
                    tag: action.tag,
                }
            };
        case 'CHANGE_ADD_WORDDISPLAY_TEXT':
            return {
                ...state,
                addWordDisplay: {
                    ...state.addWordDisplay,
                    text: action.text,
                }
            };
        case 'CHANGE_ADD_WORDDISPLAY_WIDTH':
            return {
                ...state,
                addWordDisplay: {
                    ...state.addWordDisplay,
                    width: action.width,
                }
            };
        case 'CHANGE_ADD_WORDDISPLAY_FONTSIZE':
            return {
                ...state,
                addWordDisplay: {
                    ...state.addWordDisplay,
                    fontSize: action.fontSize,
                }
            };
        case 'WORDDISPLAY_MOVE_LEFT_DECREASE':
            return {
                ...state,
                addWordDisplay: {
                    ...state.addWordDisplay,
                    left: Math.round(parseInt(state.addWordDisplay.left) / parseInt(action.offset)) * parseInt(action.offset) - parseInt(action.offset),
                }
            }
        case 'WORDDISPLAY_MOVE_LEFT_INCREASE':
            return {
                ...state,
                addWordDisplay: {
                    ...state.addWordDisplay,
                    left: Math.round(parseInt(state.addWordDisplay.left) / parseInt(action.offset)) * parseInt(action.offset) + parseInt(action.offset),
                }
            }
        case 'WORDDISPLAY_MOVE_TOP_DECREASE':
            return {
                ...state,
                addWordDisplay: {
                    ...state.addWordDisplay,
                    top: Math.round(parseInt(state.addWordDisplay.top) / parseInt(action.offset)) * parseInt(action.offset) - parseInt(action.offset),
                }
            }
        case 'WORDDISPLAY_MOVE_TOP_INCREASE':
            return {
                ...state,
                addWordDisplay: {
                    ...state.addWordDisplay,
                    top: Math.round(parseInt(state.addWordDisplay.top) / parseInt(action.offset)) * parseInt(action.offset) + parseInt(action.offset),
                }
            }
        //edit
        case 'CHANGE_EDIT_WORDDISPLAY_LEFT':
            return {
                ...state,
                editWordDisplay: {
                    ...state.editWordDisplay,
                    left: action.left,
                }
            };
        case 'CHANGE_EDIT_WORDDISPLAY_TOP':
            return {
                ...state,
                editWordDisplay: {
                    ...state.editWordDisplay,
                    top: action.top,
                }
            };
        case 'CHANGE_EDIT_WORDDISPLAY_TAG':
            return {
                ...state,
                editWordDisplay: {
                    ...state.editWordDisplay,
                    tag: action.tag,
                }
            };
        case 'CHANGE_EDIT_WORDDISPLAY_TEXT':
            return {
                ...state,
                editWordDisplay: {
                    ...state.editWordDisplay,
                    text: action.text,
                }
            };
        case 'CHANGE_EDIT_WORDDISPLAY_WIDTH':
            return {
                ...state,
                editWordDisplay: {
                    ...state.editWordDisplay,
                    width: action.width,
                }
            };
        case 'CHANGE_EDIT_WORDDISPLAY_FONTSIZE':
            return {
                ...state,
                editWordDisplay: {
                    ...state.editWordDisplay,
                    fontSize: action.fontSize,
                }
            };
        case 'EDIT_WORDDISPLAY_MOVE_LEFT_DECREASE':
            return {
                ...state,
                editWordDisplay: {
                    ...state.editWordDisplay,
                    left: Math.round(parseInt(state.editWordDisplay.left) / parseInt(action.offset)) * parseInt(action.offset) - parseInt(action.offset),
                }
            }
        case 'EDIT_WORDDISPLAY_MOVE_LEFT_INCREASE':
            return {
                ...state,
                editWordDisplay: {
                    ...state.editWordDisplay,
                    left: Math.round(parseInt(state.editWordDisplay.left) / parseInt(action.offset)) * parseInt(action.offset) + parseInt(action.offset),
                }
            }
        case 'EDIT_WORDDISPLAY_MOVE_TOP_DECREASE':
            return {
                ...state,
                editWordDisplay: {
                    ...state.editWordDisplay,
                    top: Math.round(parseInt(state.editWordDisplay.top) / parseInt(action.offset)) * parseInt(action.offset) - parseInt(action.offset),
                }
            }
        case 'EDIT_WORDDISPLAY_MOVE_TOP_INCREASE':
            return {
                ...state,
                editWordDisplay: {
                    ...state.editWordDisplay,
                    top: Math.round(parseInt(state.editWordDisplay.top) / parseInt(action.offset)) * parseInt(action.offset) + parseInt(action.offset),
                }
            }
        case 'RESET_WORDDISPLAY_EDIT_FORM':
            return {
                ...state,
                editWordDisplay: {
                    top: '',
                    left: '',
                    width: '',
                    fontSize: '',
                    tag: '',
                    text: '',
                    id: '',
                }
            }
        default:
            return state;
    }

};