const initState = {
    statusConnect: '',
    lastCommand:'...',
    fromPLC:{
        message:'',
    },
}
export const moibleReducer = (state = initState, action) => {
    switch (action.type) {
        case 'WRITE_TO_PLC':
                return {
                    ...state,
                    lastCommand:action.command
                };
        
        default:
            return state
    }
}
