const initState = {
    editImage: {
        id: '',
        src: '',
        left: '',
        top: '',
        nameElement: '',
        tag: '',
    },
    clickImage: {
        id: '',
        tag: '',
        left: '',
        top: '',
        nameElement: '',
    },
    addImage: {
        tag: '',
        left: 100,
        top: 100,
        nameImage: 'CSC',
        state: 1,
        nameElement: '',
    }

}
export const imageReducer = (state = initState, action) => {
    switch (action.type) {
        case 'TOGGLE_IMAGE':
            return {
                ...state,
                editImage: {
                    id: action.Image.id,
                    src: action.Image.src,
                    left: action.Image.left,
                    top: action.Image.top,
                    nameElement: action.Image.nameElement,
                    tag: action.Image.tag,
                }
            };
        case 'CLICK_IMAGE':
            return {
                ...state,
                clickImage: {
                    id: action.Image.id,
                    left: action.Image.left,
                    top: action.Image.top,
                    nameElement: action.Image.nameElement,
                    tag: action.Image.tag,
                }
            };
        case 'CHANGE_ADD_IMAGE_TAG':
            return {
                ...state,
                addImage: {
                    ...state.addImage,
                    tag: action.tag,
                }
            };
        case 'CHANGE_ADD_IMAGE_LEFT':
            return {
                ...state,
                addImage: {
                    ...state.addImage,
                    left: action.left,
                }
            };
        case 'CHANGE_ADD_IMAGE_TOP':
            return {
                ...state,
                addImage: {
                    ...state.addImage,
                    top: action.top,
                }
            };
        case 'CHANGE_ADD_NAME_IMAGE':
            return {
                ...state,
                addImage: {
                    ...state.addImage,
                    nameImage: action.name,
                }
            };
        case 'CHANGE_ADD_NAME_ELEMENT':
            return {
                ...state,
                addImage: {
                    ...state.addImage,
                    nameElement: action.name,
                }
            };
        case 'IMAGE_MOVE_LEFT_DECREASE':
            return {
                ...state,
                addImage: {
                    ...state.addImage,
                    left: Math.round(parseInt(state.addImage.left) / parseInt(action.offset)) * parseInt(action.offset) - parseInt(action.offset),
                }
            }
        case 'IMAGE_MOVE_LEFT_INCREASE':
            return {
                ...state,
                addImage: {
                    ...state.addImage,
                    left: Math.round(parseInt(state.addImage.left) / parseInt(action.offset)) * parseInt(action.offset) + parseInt(action.offset),
                }
            }
        case 'IMAGE_MOVE_TOP_DECREASE':
            return {
                ...state,
                addImage: {
                    ...state.addImage,
                    top: Math.round(parseInt(state.addImage.top) / parseInt(action.offset)) * parseInt(action.offset) - parseInt(action.offset),
                }
            }
        case 'IMAGE_MOVE_TOP_INCREASE':
            return {
                ...state,
                addImage: {
                    ...state.addImage,
                    top: Math.round(parseInt(state.addImage.top) / parseInt(action.offset)) * parseInt(action.offset) + parseInt(action.offset),
                }
            }
        //edit
        case 'CHANGE_EDIT_IMAGE_TAG':
            return {
                ...state,
                editImage: {
                    ...state.editImage,
                    tag: action.tag,
                }
            };
        case 'CHANGE_EDIT_IMAGE_LEFT':
            return {
                ...state,
                editImage: {
                    ...state.editImage,
                    left: action.left,
                }
            };
        case 'CHANGE_EDIT_IMAGE_TOP':
            return {
                ...state,
                editImage: {
                    ...state.editImage,
                    top: action.top,
                }
            };
        case 'CHANGE_EDIT_NAME_IMAGE':
            return {
                ...state,
                editImage: {
                    ...state.editImage,
                    nameImage: action.name,
                }
            };
        case 'CHANGE_EDIT_NAME_ELEMENT':
            return {
                ...state,
                editImage: {
                    ...state.editImage,
                    nameElement: action.name,
                }
            };
        case 'EDIT_IMAGE_MOVE_LEFT_DECREASE':
            return {
                ...state,
                editImage: {
                    ...state.editImage,
                    left: Math.round(parseInt(state.editImage.left) / parseInt(action.offset)) * parseInt(action.offset) - parseInt(action.offset),
                }
            }
        case 'EDIT_IMAGE_MOVE_LEFT_INCREASE':
            return {
                ...state,
                editImage: {
                    ...state.editImage,
                    left: Math.round(parseInt(state.editImage.left) / parseInt(action.offset)) * parseInt(action.offset) + parseInt(action.offset),
                }
            }
        case 'EDIT_IMAGE_MOVE_TOP_DECREASE':
            return {
                ...state,
                editImage: {
                    ...state.editImage,
                    top: Math.round(parseInt(state.editImage.top) / parseInt(action.offset)) * parseInt(action.offset) - parseInt(action.offset),
                }
            }
        case 'EDIT_IMAGE_MOVE_TOP_INCREASE':
            return {
                ...state,
                editImage: {
                    ...state.editImage,
                    top: Math.round(parseInt(state.editImage.top) / parseInt(action.offset)) * parseInt(action.offset) + parseInt(action.offset),
                }
            }
        case 'RESET_IMAGE_EDIT_FORM':
            return {
                ...state,
                editImage: {
                    id: '',
                    src: '',
                    left: '',
                    top: '',
                    nameElement: '',
                    tag: '',
                },
            }
        default:
            return state
    }
}
