const InitialState = {
    addBitDisplay: {
        top: '100',
        left: '100',
        width: '100',
        fontSize: '14',
        tag: '1',
        text0: '1234',
        text1: '1234',
        color0: 'blue',
        color1: '#66ff33',
        nameImage0: '',
        nameImage1: '',
        status: false,
        type: '1',
    },
    editBitDisplay: {
        top: '',
        left: '',
        width: '55',
        fontSize: '14',
        tag: '',
        text0: 'text0',
        text1: 'text1',
        color0: 'blue',
        color1: 'green',
        nameImage0: '',
        nameImage1: '',
        status: true,
        id: '',
        type: '1',
    }
};
export const bitDisplayReducer = (state = InitialState, action) => {
    switch (action.type) {
        case 'TOGGLE_BITDISPLAY':
            return {
                ...state,
                editBitDisplay: {
                    ...state.editBitDisplay,
                    left: action.bitDisplay.left,
                    top: action.bitDisplay.top,
                    width: action.bitDisplay.width,
                    fontSize: action.bitDisplay.fontSize,
                    tag: action.bitDisplay.tag,
                    text0: action.bitDisplay.text0,
                    text1: action.bitDisplay.text1,
                    color0: action.bitDisplay.color0,
                    color1: action.bitDisplay.color1,
                    nameImage0: action.bitDisplay.nameImage0,
                    nameImage1: action.bitDisplay.nameImage1,
                    status: action.bitDisplay.status,
                    type: action.bitDisplay.type,
                    id: action.bitDisplay.id,
                }
            };
        case 'CHANGE_ADD_BITDISPLAY_LEFT':
            return {
                ...state,
                addBitDisplay: {
                    ...state.addBitDisplay,
                    left: action.left,
                }
            };
        case 'CHANGE_ADD_BITDISPLAY_TOP':
            return {
                ...state,
                addBitDisplay: {
                    ...state.addBitDisplay,
                    top: action.top,
                }
            };
        case 'CHANGE_ADD_BITDISPLAY_TAG':
            return {
                ...state,
                addBitDisplay: {
                    ...state.addBitDisplay,
                    tag: action.tag,
                }
            };
        case 'CHANGE_ADD_BITDISPLAY_TEXT0':
            return {
                ...state,
                addBitDisplay: {
                    ...state.addBitDisplay,
                    text0: action.text0,
                }
            };
        case 'CHANGE_ADD_BITDISPLAY_TEXT1':
            return {
                ...state,
                addBitDisplay: {
                    ...state.addBitDisplay,
                    text1: action.text1,
                }
            };
        case 'CHANGE_ADD_BITDISPLAY_COLOR0':
            return {
                ...state,
                addBitDisplay: {
                    ...state.addBitDisplay,
                    color0: action.color0,
                }
            };
        case 'CHANGE_ADD_BITDISPLAY_COLOR1':
            return {
                ...state,
                addBitDisplay: {
                    ...state.addBitDisplay,
                    color1: action.color1,
                }
            };
        case 'CHANGE_ADD_BITDISPLAY_STATUS':
            return {
                ...state,
                addBitDisplay: {
                    ...state.addBitDisplay,
                    status: action.status,
                }
            };
        case 'CHANGE_ADD_BITDISPLAY_TYPE':
            return {
                ...state,
                addBitDisplay: {
                    ...state.addBitDisplay,
                    type: action.item,
                }
            };
        case 'CHANGE_ADD_BITDISPLAY_WIDTH':
            return {
                ...state,
                addBitDisplay: {
                    ...state.addBitDisplay,
                    width: action.width,
                }
            };
        case 'CHANGE_ADD_BITDISPLAY_FONTSIZE':
            return {
                ...state,
                addBitDisplay: {
                    ...state.addBitDisplay,
                    fontSize: action.fontSize,
                }
            };
        case 'CHANGE_ADD_BITDISPLAY_NAMEIMAGE0':
            return {
                ...state,
                addBitDisplay: {
                    ...state.addBitDisplay,
                    nameImage0: action.image,
                }
            };
        case 'CHANGE_ADD_BITDISPLAY_NAMEIMAGE1':
            return {
                ...state,
                addBitDisplay: {
                    ...state.addBitDisplay,
                    nameImage1: action.image,
                }
            };
        case 'BITDISPLAY_MOVE_LEFT_DECREASE':
            return {
                ...state,
                addBitDisplay: {
                    ...state.addBitDisplay,
                    left: Math.round(parseInt(state.addBitDisplay.left) / parseInt(action.offset)) * parseInt(action.offset) - parseInt(action.offset),
                }
            }
        case 'BITDISPLAY_MOVE_LEFT_INCREASE':
            return {
                ...state,
                addBitDisplay: {
                    ...state.addBitDisplay,
                    left: Math.round(parseInt(state.addBitDisplay.left) / parseInt(action.offset)) * parseInt(action.offset) + parseInt(action.offset),
                }
            }
        case 'BITDISPLAY_MOVE_TOP_DECREASE':
            return {
                ...state,
                addBitDisplay: {
                    ...state.addBitDisplay,
                    top: Math.round(parseInt(state.addBitDisplay.top) / parseInt(action.offset)) * parseInt(action.offset) - parseInt(action.offset),
                }
            }
        case 'BITDISPLAY_MOVE_TOP_INCREASE':
            return {
                ...state,
                addBitDisplay: {
                    ...state.addBitDisplay,
                    top: Math.round(parseInt(state.addBitDisplay.top) / parseInt(action.offset)) * parseInt(action.offset) + parseInt(action.offset),
                }
            }
        //edit
        case 'CHANGE_EDIT_BITDISPLAY_LEFT':
            return {
                ...state,
                editBitDisplay: {
                    ...state.editBitDisplay,
                    left: action.left,
                }
            };
        case 'CHANGE_EDIT_BITDISPLAY_TOP':
            return {
                ...state,
                editBitDisplay: {
                    ...state.editBitDisplay,
                    top: action.top,
                }
            };
        case 'CHANGE_EDIT_BITDISPLAY_TAG':
            return {
                ...state,
                editBitDisplay: {
                    ...state.editBitDisplay,
                    tag: action.tag,
                }
            };
        case 'CHANGE_EDIT_BITDISPLAY_TEXT0':
            return {
                ...state,
                editBitDisplay: {
                    ...state.editBitDisplay,
                    text0: action.text0,
                }
            };
        case 'CHANGE_EDIT_BITDISPLAY_TEXT1':
            return {
                ...state,
                editBitDisplay: {
                    ...state.editBitDisplay,
                    text1: action.text1,
                }
            };
        case 'CHANGE_EDIT_BITDISPLAY_COLOR0':
            return {
                ...state,
                editBitDisplay: {
                    ...state.editBitDisplay,
                    color0: action.color0,
                }
            };
        case 'CHANGE_EDIT_BITDISPLAY_COLOR1':
            return {
                ...state,
                editBitDisplay: {
                    ...state.editBitDisplay,
                    color1: action.color1,
                }
            };
        case 'CHANGE_EDIT_BITDISPLAY_STATUS':
            return {
                ...state,
                editBitDisplay: {
                    ...state.editBitDisplay,
                    status: action.status,
                }
            };
        case 'CHANGE_EDIT_BITDISPLAY_TYPE':
            return {
                ...state,
                editBitDisplay: {
                    ...state.editBitDisplay,
                    type: action.item,
                }
            };
        case 'CHANGE_EDIT_BITDISPLAY_WIDTH':
            return {
                ...state,
                editBitDisplay: {
                    ...state.editBitDisplay,
                    width: action.width,
                }
            };
        case 'CHANGE_EDIT_BITDISPLAY_FONTSIZE':
            return {
                ...state,
                editBitDisplay: {
                    ...state.editBitDisplay,
                    fontSize: action.fontSize,
                }
            };
        case 'CHANGE_EDIT_BITDISPLAY_NAMEIMAGE0':
            return {
                ...state,
                editBitDisplay: {
                    ...state.editBitDisplay,
                    nameImage0: action.image,
                }
            };
        case 'CHANGE_EDIT_BITDISPLAY_NAMEIMAGE1':
            return {
                ...state,
                editBitDisplay: {
                    ...state.editBitDisplay,
                    nameImage1: action.image,
                }
            };
        case 'EDIT_BITDISPLAY_MOVE_LEFT_DECREASE':
            return {
                ...state,
                editBitDisplay: {
                    ...state.editBitDisplay,
                    left: Math.round(parseInt(state.editBitDisplay.left) / parseInt(action.offset)) * parseInt(action.offset) - parseInt(action.offset),
                }
            }
        case 'EDIT_BITDISPLAY_MOVE_LEFT_INCREASE':
            return {
                ...state,
                editBitDisplay: {
                    ...state.editBitDisplay,
                    left: Math.round(parseInt(state.editBitDisplay.left) / parseInt(action.offset)) * parseInt(action.offset) + parseInt(action.offset),
                }
            }
        case 'EDIT_BITDISPLAY_MOVE_TOP_DECREASE':
            return {
                ...state,
                editBitDisplay: {
                    ...state.editBitDisplay,
                    top: Math.round(parseInt(state.editBitDisplay.top) / parseInt(action.offset)) * parseInt(action.offset) - parseInt(action.offset),
                }
            }
        case 'EDIT_BITDISPLAY_MOVE_TOP_INCREASE':
            return {
                ...state,
                editBitDisplay: {
                    ...state.editBitDisplay,
                    top: Math.round(parseInt(state.editBitDisplay.top) / parseInt(action.offset)) * parseInt(action.offset) + parseInt(action.offset),
                }
            }
        case 'RESET_BITDISPLAY_EDIT_FORM':
            return {
                ...state,
                editBitDisplay: {
                    top: '',
                    left: '',
                    width: '',
                    fontSize: '',
                    tag: '',
                    text0: '',
                    text1: '',
                    color0: '',
                    color1: '',
                    nameImage0: '',
                    nameImage1: '',
                    status: true,
                    id: '',
                    type: '1',
                }
            }
        default:
            return state;
    }

};