const InitialState = {
    addDWordDisplay: {
        top: '100',
        left: '100',
        width: '100',
        fontSize: '14',
        tag: '1',
        text: '1234'
    },
    editDWordDisplay: {
        top: '',
        left: '',
        width: '',
        fontSize: '',
        tag: '',
        text: '',
        id: '',
    }
};
export const dwordDisplayReducer = (state = InitialState, action) => {
    switch (action.type) {
        case 'TOGGLE_DWORDDISPLAY':
            console.log('action dword', action.editDWordDisplay)
            return {
                ...state,
                editDWordDisplay: {
                    ...state.editDWordDisplay,
                    left: action.editDWordDisplay.left,
                    top: action.editDWordDisplay.top,
                    width: action.editDWordDisplay.width,
                    fontSize: action.editDWordDisplay.fontSize,
                    tag: action.editDWordDisplay.tag,
                    text: action.editDWordDisplay.text,
                    id: action.editDWordDisplay.id,
                }
            };
        case 'CHANGE_ADD_DWORDDISPLAY_LEFT':
            return {
                ...state,
                addDWordDisplay: {
                    ...state.addDWordDisplay,
                    left: action.left,
                }
            };
        case 'CHANGE_ADD_DWORDDISPLAY_TOP':
            return {
                ...state,
                addDWordDisplay: {
                    ...state.addDWordDisplay,
                    top: action.top,
                }
            };
        case 'CHANGE_ADD_DWORDDISPLAY_TAG':
            return {
                ...state,
                addDWordDisplay: {
                    ...state.addDWordDisplay,
                    tag: action.tag,
                }
            };
        case 'CHANGE_ADD_DWORDDISPLAY_TEXT':
            return {
                ...state,
                addDWordDisplay: {
                    ...state.addDWordDisplay,
                    text: action.text,
                }
            };
        case 'CHANGE_ADD_DWORDDISPLAY_WIDTH':
            return {
                ...state,
                addDWordDisplay: {
                    ...state.addDWordDisplay,
                    width: action.width,
                }
            };
        case 'CHANGE_ADD_DWORDDISPLAY_FONTSIZE':
            return {
                ...state,
                addDWordDisplay: {
                    ...state.addDWordDisplay,
                    fontSize: action.fontSize,
                }
            };
        case 'DWORDDISPLAY_MOVE_LEFT_DECREASE':
            return {
                ...state,
                addDWordDisplay: {
                    ...state.addDWordDisplay,
                    left: Math.round(parseInt(state.addDWordDisplay.left) / parseInt(action.offset)) * parseInt(action.offset) - parseInt(action.offset),
                }
            }
        case 'DWORDDISPLAY_MOVE_LEFT_INCREASE':
            return {
                ...state,
                addDWordDisplay: {
                    ...state.addDWordDisplay,
                    left: Math.round(parseInt(state.addDWordDisplay.left) / parseInt(action.offset)) * parseInt(action.offset) + parseInt(action.offset),
                }
            }
        case 'DWORDDISPLAY_MOVE_TOP_DECREASE':
            return {
                ...state,
                addDWordDisplay: {
                    ...state.addDWordDisplay,
                    top: Math.round(parseInt(state.addDWordDisplay.top) / parseInt(action.offset)) * parseInt(action.offset) - parseInt(action.offset),
                }
            }
        case 'DWORDDISPLAY_MOVE_TOP_INCREASE':
            return {
                ...state,
                addDWordDisplay: {
                    ...state.addDWordDisplay,
                    top: Math.round(parseInt(state.addDWordDisplay.top) / parseInt(action.offset)) * parseInt(action.offset) + parseInt(action.offset),
                }
            }
        //edit
        case 'CHANGE_EDIT_DWORDDISPLAY_LEFT':
            return {
                ...state,
                editDWordDisplay: {
                    ...state.editDWordDisplay,
                    left: action.left,
                }
            };
        case 'CHANGE_EDIT_DWORDDISPLAY_TOP':
            return {
                ...state,
                editDWordDisplay: {
                    ...state.editDWordDisplay,
                    top: action.top,
                }
            };
        case 'CHANGE_EDIT_DWORDDISPLAY_TAG':
            return {
                ...state,
                editDWordDisplay: {
                    ...state.editDWordDisplay,
                    tag: action.tag,
                }
            };
        case 'CHANGE_EDIT_DWORDDISPLAY_TEXT':
            return {
                ...state,
                editDWordDisplay: {
                    ...state.editDWordDisplay,
                    text: action.text,
                }
            };
        case 'CHANGE_EDIT_DWORDDISPLAY_WIDTH':
            return {
                ...state,
                editDWordDisplay: {
                    ...state.editDWordDisplay,
                    width: action.width,
                }
            };
        case 'CHANGE_EDIT_DWORDDISPLAY_FONTSIZE':
            return {
                ...state,
                editDWordDisplay: {
                    ...state.editDWordDisplay,
                    fontSize: action.fontSize,
                }
            };
        case 'EDIT_DWORDDISPLAY_MOVE_LEFT_DECREASE':
            return {
                ...state,
                editDWordDisplay: {
                    ...state.editDWordDisplay,
                    left: Math.round(parseInt(state.editDWordDisplay.left) / parseInt(action.offset)) * parseInt(action.offset) - parseInt(action.offset),
                }
            }
        case 'EDIT_DWORDDISPLAY_MOVE_LEFT_INCREASE':
            return {
                ...state,
                editDWordDisplay: {
                    ...state.editDWordDisplay,
                    left: Math.round(parseInt(state.editDWordDisplay.left) / parseInt(action.offset)) * parseInt(action.offset) + parseInt(action.offset),
                }
            }
        case 'EDIT_DWORDDISPLAY_MOVE_TOP_DECREASE':
            return {
                ...state,
                editDWordDisplay: {
                    ...state.editDWordDisplay,
                    top: Math.round(parseInt(state.editDWordDisplay.top) / parseInt(action.offset)) * parseInt(action.offset) - parseInt(action.offset),
                }
            }
        case 'EDIT_DWORDDISPLAY_MOVE_TOP_INCREASE':
            return {
                ...state,
                editDWordDisplay: {
                    ...state.editDWordDisplay,
                    top: Math.round(parseInt(state.editDWordDisplay.top) / parseInt(action.offset)) * parseInt(action.offset) + parseInt(action.offset),
                }
            }
        case 'RESET_DWORDDISPLAY_EDIT_FORM':
            return {
                ...state,
                editDWordDisplay: {
                    top: '',
                    left: '',
                    width: '',
                    fontSize: '',
                    tag: '',
                    text: '',
                    id: '',
                }
            }
        default:
            return state;
    }

};