const initState = {
    idAdmin: '1',//'S5sPGpvaNuWQXms4iPH8VXo3pu93',
    authError: null,
    addImage: false,
    editImage: false,
    addLabel: false,
    editLabel: false,
    addLine: false,
    editLine: false,
    addWordDisplay: false,
    editWordDisplay: false,
    addWordAdjust: false,
    editWordAdjust: false,
    addDWordDisplay: false,
    editDWordDisplay: false,
    addDWordAdjust: false,
    editDWordAdjust: false,
    addBitDisplay: false,
    editBitDisplay: false,
    addBitAdjust: false,
    editBitAdjust: false,
    modeSpeech: 1,
    editMode: false,
    addMode: false,
    addPanel: false,
    editPanel: false,
    addRangeSlider: false,
    editRangeSlider: false,
    addJoystick: false,
    editJoystick: false,
}
export const authReducer = (state = initState, action) => {
    switch (action.type) {
        case 'OFF_ALL_STATE':
            return {
                ...state,
                addImage: false,
                editImage: false,
                addLabel: false,
                editLabel: false,
                addLine: false,
                editLine: false,
                addWordDisplay: false,
                editWordDisplay: false,
                addWordAdjust: false,
                editWordAdjust: false,
                addDWordDisplay: false,
                editDWordDisplay: false,
                addDWordAdjust: false,
                editDWordAdjust: false,
                addBitDisplay: false,
                editBitDisplay: false,
                addBitAdjust: false,
                editBitAdjust: false,
                editMode: false,
                addMode: false,
                addPanel: false,
                editPanel: false,
                addRangeSlider: false,
                editRangeSlider: false,
                addJoystick: false,
                editJoystick: false,
            }
        case 'CHANGE_MODE_SPEECH':
            return {
                ...state,
                modeSpeech: state.modeSpeech * (-1)
            }
        case 'LOGIN_ERROR':
            console.log('login err');
            return {
                ...state,
                authError: action.err.message
            };
        case 'LOGIN_SUCCESS':
            console.log('login success');
            return {
                ...state,
                authError: null
            };
        case 'SIGN_OUT_SUCCESS':
            return {
                ...state,
                authError: ''
            };
        case 'SIGN_OUT_ERROR':
            return {
                ...state,
                authError: 'SIGN OUT ERROR'
            };
        case 'SIGNUP_SUCCESS':
            console.log('SIGN UP success');
            return {
                ...state,
                authError: null
            };
        case 'SIGNUP_ERROR':
            console.log('SIGN UP error', action.err);
            return {
                ...state,
                authError: action.err.message
            };
        case 'PROFILE_SUCCESS':
            console.log('Update Profile Success');
            return {
                ...state,
                authError: ''
            };
        case 'PROFILE_ERROR':
            console.log('UPDATE PROFILE ERROR', action.err);
            return {
                ...state,
                authError: action.err.message
            };
        case 'TOGGLE_ADD_IMAGE':
            return {
                ...state,
                addImage: true,
                addMode: true,
            };
        case 'TOGGLE_EDIT_IMAGE':
            return {
                ...state,
                editImage: true,
                editMode: true,
            };
        case 'TOGGLE_ADD_LABEL':
            return {
                ...state,
                addLabel: true,
                addMode: true,
            };
        case 'TOGGLE_EDIT_LABEL':
            return {
                ...state,
                editLabel: true,
                editMode: true,
            };
        case 'TOGGLE_ADD_LINE':
            return {
                ...state,
                addLine: true,
                addMode: true,
            };
        case 'TOGGLE_EDIT_LINE':
            return {
                ...state,
                editLine: true,
                editMode: true,
            };
        case 'TOGGLE_ADD_WORDDISPLAY':
            return {
                ...state,
                addWordDisplay: true,
                addMode: true,
            };
        case 'TOGGLE_EDIT_WORDDISPLAY':
            return {
                ...state,
                editWordDisplay: true,
                editMode: true,
            };
        case 'TOGGLE_ADD_WORDADJUST':
            return {
                ...state,
                addWordAdjust: true,
                addMode: true,
            };
        case 'TOGGLE_EDIT_WORDADJUST':
            return {
                ...state,
                editWordAdjust: true,
                editMode: true,
            };
        case 'TOGGLE_ADD_DWORDDISPLAY':
            return {
                ...state,
                addDWordDisplay: true,
                addMode: true,
            };
        case 'TOGGLE_EDIT_DWORDDISPLAY':
            return {
                ...state,
                editDWordDisplay: true,
                editMode: true,
            };
        case 'TOGGLE_ADD_DWORDADJUST':
            return {
                ...state,
                addDWordAdjust: true,
                addMode: true,
            };
        case 'TOGGLE_EDIT_DWORDADJUST':
            return {
                ...state,
                editDWordAdjust: true,
                editMode: true,
            };
        case 'TOGGLE_ADD_BITDISPLAY':
            return {
                ...state,
                addBitDisplay: true,
                addMode: true,
            };
        case 'TOGGLE_EDIT_BITDISPLAY':
            return {
                ...state,
                editBitDisplay: true,
                editMode: true,
            };
        case 'TOGGLE_ADD_BITADJUST':
            return {
                ...state,
                addBitAdjust: true,
                addMode: true,
            };
        case 'TOGGLE_EDIT_BITADJUST':
            return {
                ...state,
                editBitAdjust: true,
                editMode: true,
            };
        case 'TOGGLE_ADD_PANEL':
            return {
                ...state,
                addPanel: true,
                addMode: true,
            };
        case 'TOGGLE_EDIT_PANEL':
            return {
                ...state,
                editPanel: true,
                editMode: true,
            };
        case 'TOGGLE_ADD_RANGE_SLIDER':
            return {
                ...state,
                addRangeSlider: true,
                addMode: true,
            };
        case 'TOGGLE_EDIT_RANGE_SLIDER':
            return {
                ...state,
                editRangeSlider: true,
                editMode: true,
            };
        case 'TOGGLE_ADD_JOYSTICK':
            return {
                ...state,
                addJoystick: true,
                addMode: true,
            };
        case 'TOGGLE_EDIT_JOYSTICK':
            return {
                ...state,
                editJoystick: true,
                editMode: true,
            };
        default:
            return state
    }
}