const InitialState = {
    addBitAdjust: {
        top: '100',
        left: '100',
        width: '70',
        height: '30',
        fontSize: '14',
        tag: '1',
        text: '1234',
        type: 2
    },
    editBitAdjust: {
        top: '',
        left: '',
        width: '',
        height: '',
        fontSize: '',
        tag: '',
        text: '',
        id: '',
        type: '',
    },
    clickWordAdjust: {
        top: '',
        left: '',
        width: '',
        height: '',
        fontSize: '',
        tag: '',
        text: '',
        id: '',
    }
};
export const bitAdjustReducer = (state = InitialState, action) => {
    switch (action.type) {
        case 'TOGGLE_BITADJUST':
            return {
                ...state,
                editBitAdjust: {
                    ...state.editBitAdjust,
                    left: action.bitAdjust.left,
                    top: action.bitAdjust.top,
                    width: action.bitAdjust.width,
                    height: action.bitAdjust.height,
                    fontSize: action.bitAdjust.fontSize,
                    tag: action.bitAdjust.tag,
                    text: action.bitAdjust.text,
                    id: action.bitAdjust.id,
                    type: action.bitAdjust.type,
                }
            };
        //ADD
        case 'CHANGE_ADD_BITADJUST_LEFT':
            return {
                ...state,
                addBitAdjust: {
                    ...state.addBitAdjust,
                    left: action.left,
                }
            };
        case 'CHANGE_ADD_BITADJUST_TOP':
            return {
                ...state,
                addBitAdjust: {
                    ...state.addBitAdjust,
                    top: action.top,
                }
            };
        case 'CHANGE_ADD_BITADJUST_TAG':
            return {
                ...state,
                addBitAdjust: {
                    ...state.addBitAdjust,
                    tag: action.tag,
                }
            };
        case 'CHANGE_ADD_BITADJUST_TEXT':
            return {
                ...state,
                addBitAdjust: {
                    ...state.addBitAdjust,
                    text: action.text,
                }
            };
        case 'CHANGE_ADD_BITADJUST_WIDTH':
            return {
                ...state,
                addBitAdjust: {
                    ...state.addBitAdjust,
                    width: action.width,
                }
            };
        case 'CHANGE_ADD_BITADJUST_HEIGHT':
            return {
                ...state,
                addBitAdjust: {
                    ...state.addBitAdjust,
                    height: action.height,
                }
            };
        case 'CHANGE_ADD_BITADJUST_FONTSIZE':
            return {
                ...state,
                addBitAdjust: {
                    ...state.addBitAdjust,
                    fontSize: action.fontSize,
                }
            };
        case 'CHANGE_ADD_BITADJUST_TYPE':
            return {
                ...state,
                addBitAdjust: {
                    ...state.addBitAdjust,
                    type: action.item,
                }
            };
        case 'BITADJUST_MOVE_LEFT_DECREASE':
            return {
                ...state,
                addBitAdjust: {
                    ...state.addBitAdjust,
                    left: Math.round(parseInt(state.addBitAdjust.left) / parseInt(action.offset)) * parseInt(action.offset) - parseInt(action.offset),
                }
            }
        case 'BITADJUST_MOVE_LEFT_INCREASE':
            return {
                ...state,
                addBitAdjust: {
                    ...state.addBitAdjust,
                    left: Math.round(parseInt(state.addBitAdjust.left) / parseInt(action.offset)) * parseInt(action.offset) + parseInt(action.offset),
                }
            }
        case 'BITADJUST_MOVE_TOP_DECREASE':
            return {
                ...state,
                addBitAdjust: {
                    ...state.addBitAdjust,
                    top: Math.round(parseInt(state.addBitAdjust.top) / parseInt(action.offset)) * parseInt(action.offset) - parseInt(action.offset),
                }
            }
        case 'BITADJUST_MOVE_TOP_INCREASE':
            console.log(parseInt(action.offset))
            return {
                ...state,
                addBitAdjust: {
                    ...state.addBitAdjust,
                    top: Math.round(parseInt(state.addBitAdjust.top) / parseInt(action.offset)) * parseInt(action.offset) + parseInt(action.offset),
                }
            }
        //Edit
        case 'CHANGE_EDIT_BITADJUST_LEFT':
            return {
                ...state,
                editBitAdjust: {
                    ...state.editBitAdjust,
                    left: action.left,
                }
            };
        case 'CHANGE_EDIT_BITADJUST_TOP':
            return {
                ...state,
                editBitAdjust: {
                    ...state.editBitAdjust,
                    top: action.top,
                }
            };
        case 'CHANGE_EDIT_BITADJUST_TAG':
            return {
                ...state,
                editBitAdjust: {
                    ...state.editBitAdjust,
                    tag: action.tag,
                }
            };
        case 'CHANGE_EDIT_BITADJUST_TEXT':
            return {
                ...state,
                editBitAdjust: {
                    ...state.editBitAdjust,
                    text: action.text,
                }
            };
        case 'CHANGE_EDIT_BITADJUST_WIDTH':
            return {
                ...state,
                editBitAdjust: {
                    ...state.editBitAdjust,
                    width: action.width,
                }
            };
        case 'CHANGE_EDIT_BITADJUST_HEIGHT':
            return {
                ...state,
                editBitAdjust: {
                    ...state.editBitAdjust,
                    height: action.height,
                }
            };
        case 'CHANGE_EDIT_BITADJUST_FONTSIZE':
            return {
                ...state,
                editBitAdjust: {
                    ...state.editBitAdjust,
                    fontSize: action.fontSize,
                }
            };
        case 'CHANGE_EDIT_BITADJUST_TYPE':
            return {
                ...state,
                editBitAdjust: {
                    ...state.editBitAdjust,
                    type: parseInt(action.item),
                }
            };
        case 'EDIT_BITADJUST_MOVE_LEFT_DECREASE':
            return {
                ...state,
                editBitAdjust: {
                    ...state.editBitAdjust,
                    left: Math.round(parseInt(state.editBitAdjust.left) / parseInt(action.offset)) * parseInt(action.offset) - parseInt(action.offset),
                }
            }
        case 'EDIT_BITADJUST_MOVE_LEFT_INCREASE':
            return {
                ...state,
                editBitAdjust: {
                    ...state.editBitAdjust,
                    left: Math.round(parseInt(state.editBitAdjust.left) / parseInt(action.offset)) * parseInt(action.offset) + parseInt(action.offset),
                }
            }
        case 'EDIT_BITADJUST_MOVE_TOP_DECREASE':
            return {
                ...state,
                editBitAdjust: {
                    ...state.editBitAdjust,
                    top: Math.round(parseInt(state.editBitAdjust.top) / parseInt(action.offset)) * parseInt(action.offset) - parseInt(action.offset),
                }
            }
        case 'EDIT_BITADJUST_MOVE_TOP_INCREASE':
            return {
                ...state,
                editBitAdjust: {
                    ...state.editBitAdjust,
                    top: Math.round(parseInt(state.editBitAdjust.top) / parseInt(action.offset)) * parseInt(action.offset) + parseInt(action.offset),
                }
            }
        case 'RESET_BITADJUST_EDIT_FORM':
            return {
                ...state,
                editBitAdjust: {
                    top: '',
                    left: '',
                    width: '',
                    height: '',
                    fontSize: '',
                    tag: '',
                    text: '',
                    id: '',
                    type: '',
                },
            }
        default:
            return state;
    }

};