const InitialState = {
    addDWordAdjust: {
        top: '100',
        left: '100',
        width: '100',
        height: '30',
        fontSize: '14',
        tag: '1',
        text: '1234'
    },
    editDWordAdjust: {
        top: '',
        left: '',
        width: '',
        height: '',
        fontSize: '',
        tag: '',
        text: '',
        id: '',
    },
    clickDWordAdjust: {
        top: '',
        left: '',
        width: '',
        height: '',
        fontSize: '',
        tag: '',
        text: '',
        id: '',
    }
};
export const dwordAdjustReducer = (state = InitialState, action) => {
    switch (action.type) {
        case 'TOGGLE_DWORDADJUST':
            return {
                ...state,
                editDWordAdjust: {
                    ...state.editDWordAdjust,
                    left: action.dwordAdjust.left,
                    top: action.dwordAdjust.top,
                    width: action.dwordAdjust.width,
                    height: action.dwordAdjust.height,
                    fontSize: action.dwordAdjust.fontSize,
                    tag: action.dwordAdjust.tag,
                    text: action.dwordAdjust.text,
                    id: action.dwordAdjust.id,
                }
            };
        case 'CLICK_DWORDADJUST':
            return {
                ...state,
                clickDWordAdjust: {
                    left: action.dwordAdjust.left,
                    top: action.dwordAdjust.top,
                    width: action.dwordAdjust.width,
                    height: action.dwordAdjust.height,
                    fontSize: action.dwordAdjust.fontSize,
                    tag: action.dwordAdjust.tag,
                    text: action.dwordAdjust.text,
                    id: action.dwordAdjust.id,
                }
            };
        case 'CHANGE_ADD_DWORDADJUST_LEFT':
            return {
                ...state,
                addDWordAdjust: {
                    ...state.addDWordAdjust,
                    left: action.left,
                }
            };
        case 'CHANGE_ADD_DWORDADJUST_TOP':
            return {
                ...state,
                addDWordAdjust: {
                    ...state.addDWordAdjust,
                    top: action.top,
                }
            };
        case 'CHANGE_ADD_DWORDADJUST_TAG':
            return {
                ...state,
                addDWordAdjust: {
                    ...state.addDWordAdjust,
                    tag: action.tag,
                }
            };
        case 'CHANGE_ADD_DWORDADJUST_TEXT':
            return {
                ...state,
                addDWordAdjust: {
                    ...state.addDWordAdjust,
                    text: action.text,
                }
            };
        case 'CHANGE_ADD_DWORDADJUST_WIDTH':
            return {
                ...state,
                addDWordAdjust: {
                    ...state.addDWordAdjust,
                    width: action.width,
                }
            };
        case 'CHANGE_ADD_DWORDADJUST_HEIGHT':
            return {
                ...state,
                addDWordAdjust: {
                    ...state.addDWordAdjust,
                    height: action.height,
                }
            };
        case 'CHANGE_ADD_DWORDADJUST_FONTSIZE':
            return {
                ...state,
                addDWordAdjust: {
                    ...state.addDWordAdjust,
                    fontSize: action.fontSize,
                }
            };
        case 'DWORDADJUST_MOVE_LEFT_DECREASE':
            return {
                ...state,
                addDWordAdjust: {
                    ...state.addDWordAdjust,
                    left: Math.round(parseInt(state.addDWordAdjust.left) / parseInt(action.offset)) * parseInt(action.offset) - parseInt(action.offset),
                }
            }
        case 'DWORDADJUST_MOVE_LEFT_INCREASE':
            return {
                ...state,
                addDWordAdjust: {
                    ...state.addDWordAdjust,
                    left: Math.round(parseInt(state.addDWordAdjust.left) / parseInt(action.offset)) * parseInt(action.offset) + parseInt(action.offset),
                }
            }
        case 'DWORDADJUST_MOVE_TOP_DECREASE':
            return {
                ...state,
                addDWordAdjust: {
                    ...state.addDWordAdjust,
                    top: Math.round(parseInt(state.addDWordAdjust.top) / parseInt(action.offset)) * parseInt(action.offset) - parseInt(action.offset),
                }
            }
        case 'DWORDADJUST_MOVE_TOP_INCREASE':
            return {
                ...state,
                addDWordAdjust: {
                    ...state.addDWordAdjust,
                    top: Math.round(parseInt(state.addDWordAdjust.top) / parseInt(action.offset)) * parseInt(action.offset) + parseInt(action.offset),
                }
            }
        //edit
        case 'CHANGE_EDIT_DWORDADJUST_LEFT':
            return {
                ...state,
                editDWordAdjust: {
                    ...state.editDWordAdjust,
                    left: action.left,
                }
            };
        case 'CHANGE_EDIT_DWORDADJUST_TOP':
            return {
                ...state,
                editDWordAdjust: {
                    ...state.editDWordAdjust,
                    top: action.top,
                }
            };
        case 'CHANGE_EDIT_DWORDADJUST_TAG':
            return {
                ...state,
                editDWordAdjust: {
                    ...state.editDWordAdjust,
                    tag: action.tag,
                }
            };
        case 'CHANGE_EDIT_DWORDADJUST_TEXT':
            return {
                ...state,
                editDWordAdjust: {
                    ...state.editDWordAdjust,
                    text: action.text,
                }
            };
        case 'CHANGE_EDIT_DWORDADJUST_WIDTH':
            return {
                ...state,
                editDWordAdjust: {
                    ...state.editDWordAdjust,
                    width: action.width,
                }
            };
        case 'CHANGE_EDIT_DWORDADJUST_HEIGHT':
            return {
                ...state,
                editDWordAdjust: {
                    ...state.editDWordAdjust,
                    height: action.height,
                }
            };
        case 'CHANGE_EDIT_DWORDADJUST_FONTSIZE':
            return {
                ...state,
                editDWordAdjust: {
                    ...state.editDWordAdjust,
                    fontSize: action.fontSize,
                }
            };
        case 'EDIT_DWORDADJUST_MOVE_LEFT_DECREASE':
            return {
                ...state,
                editDWordAdjust: {
                    ...state.editDWordAdjust,
                    left: Math.round(parseInt(state.editDWordAdjust.left) / parseInt(action.offset)) * parseInt(action.offset) - parseInt(action.offset),
                }
            }
        case 'EDIT_DWORDADJUST_MOVE_LEFT_INCREASE':
            return {
                ...state,
                editDWordAdjust: {
                    ...state.editDWordAdjust,
                    left: Math.round(parseInt(state.editDWordAdjust.left) / parseInt(action.offset)) * parseInt(action.offset) + parseInt(action.offset),
                }
            }
        case 'EDIT_DWORDADJUST_MOVE_TOP_DECREASE':
            return {
                ...state,
                editDWordAdjust: {
                    ...state.editDWordAdjust,
                    top: Math.round(parseInt(state.editDWordAdjust.top) / parseInt(action.offset)) * parseInt(action.offset) - parseInt(action.offset),
                }
            }
        case 'EDIT_DWORDADJUST_MOVE_TOP_INCREASE':
            return {
                ...state,
                editDWordAdjust: {
                    ...state.editDWordAdjust,
                    top: Math.round(parseInt(state.editDWordAdjust.top) / parseInt(action.offset)) * parseInt(action.offset) + parseInt(action.offset),
                }
            }
        case 'RESET_DWORDADJUST_EDIT_FORM':
            return {
                ...state,
                editDWordAdjust: {
                    top: '',
                    left: '',
                    width: '',
                    height: '',
                    fontSize: '',
                    tag: '',
                    text: '',
                    id: '',
                },
            }
        default:
            return state;
    }

};