import {combineReducers} from 'redux'
import { noteReducer } from './noteReducer';
import {firestoreReducer} from 'redux-firestore'
import {firebaseReducer} from 'react-redux-firebase'
import { authReducer } from './authReducer';
import {imageReducer} from './imageReducer';
import {labelReducer} from './labelReducer'
import { chartReducer } from './chartReducer';
import { navReducer } from './navReducer';
import {lineReducer} from './lineReducer'
import {backgroundReducer} from './backgroundReducer'
import {searchTimeReducer} from './searchTimeReducer'
import {moibleReducer} from './mobileReducer'
import {wordDisplayReducer} from './wordDisplayReducer'
import {dwordDisplayReducer} from './dwordDisplayReducer'
import {wordAdjustReducer} from './wordAdjustReducer'
import {dwordAdjustReducer} from './dwordAdjustReducer'
import {bitAdjustReducer} from './bitAdjustReducer'
import {bitDisplayReducer} from './bitDisplayReducer'
import { elementReducer } from './elementReducer';
import { snackBarReducer } from './snackBarReducer';
import { panelReducer } from './panelReducer';
const rootReducer=combineReducers({
    auth:authReducer,
    note:noteReducer,
    image:imageReducer,
    label:labelReducer,
    line:lineReducer,
    background:backgroundReducer,
    navPageFull:navReducer,
    firestore:firestoreReducer,
    firebase:firebaseReducer,
    chartData:chartReducer,
    searchTime:searchTimeReducer,
    mobileRobot:moibleReducer,
    wordDisplay:wordDisplayReducer,
    dwordDisplay:dwordDisplayReducer,
    wordAdjust:wordAdjustReducer,
    dwordAdjust:dwordAdjustReducer,
    bitAdjust:bitAdjustReducer,
    bitDisplay:bitDisplayReducer,
    element:elementReducer,
    snackBar:snackBarReducer,
    panel:panelReducer
});
export default rootReducer;