const initState = {
    editPanel: {
        id: '',
        width: '',
        height: '',
        left: '',
        top: '',
        margin: '',
        backgroundColor: '',
        padding: '',
        borderRadius: '',
        boxShadow1: '',
        boxShadow2: '',
        boxShadow3: '',
        boxShadowColor: '',
        zIndex: '',
    },
    addPanel: {
        width: 100,
        height: 100,
        left: 100,
        top: 100,
        margin: 10,
        backgroundColor: '#4c7549',
        padding: 10,
        borderRadius: 5,
        boxShadow1: 10,
        boxShadow2: 10,
        boxShadow3: 20,
        boxShadowColor: '#100',
        zIndex: 0,
    }

}
export const panelReducer = (state = initState, action) => {
    switch (action.type) {
        case 'TOGGLE_PANEL':
            return {
                ...state,
                editPanel: {
                    id: action.Panel.id,
                    width: action.Panel.width,
                    height: action.Panel.height,
                    left: action.Panel.left,
                    top: action.Panel.top,
                    margin: action.Panel.margin,
                    backgroundColor: action.Panel.backgroundColor,
                    padding: action.Panel.padding,
                    borderRadius: action.Panel.borderRadius,
                    boxShadow1: action.Panel.boxShadow1,
                    boxShadow2: action.Panel.boxShadow2,
                    boxShadow3: action.Panel.boxShadow3,
                    boxShadowColor: action.Panel.boxShadowColor,
                    zIndex: action.Panel.zIndex,
                }

            };
        case 'CHANGE_ADD_PANEL_LEFT':
            return {
                ...state,
                addPanel: {
                    ...state.addPanel,
                    left: action.left,
                }
            };
        case 'CHANGE_ADD_PANEL_TOP':
            return {
                ...state,
                addPanel: {
                    ...state.addPanel,
                    top: action.top,
                }
            };
        case 'CHANGE_ADD_PANEL_WIDTH':
            return {
                ...state,
                addPanel: {
                    ...state.addPanel,
                    width: action.width,
                }
            };
        case 'CHANGE_ADD_PANEL_HEIGHT':
            return {
                ...state,
                addPanel: {
                    ...state.addPanel,
                    height: action.height,
                }
            };
        case 'CHANGE_ADD_PANEL_BACKGROUNDCOLOR':
            return {
                ...state,
                addPanel: {
                    ...state.addPanel,
                    backgroundColor: action.backgroundColor,
                }
            };
        case 'CHANGE_ADD_PANEL_BORDERRADIUS':
            return {
                ...state,
                addPanel: {
                    ...state.addPanel,
                    borderRadius: action.borderRadius,
                }
            };
        case 'CHANGE_ADD_PANEL_BOXSHADOW1':
            return {
                ...state,
                addPanel: {
                    ...state.addPanel,
                    boxShadow1: action.boxShadow1,
                }
            };
        case 'CHANGE_ADD_PANEL_BOXSHADOW2':
            return {
                ...state,
                addPanel: {
                    ...state.addPanel,
                    boxShadow2: action.boxShadow2,
                }
            };
        case 'CHANGE_ADD_PANEL_BOXSHADOW3':
            return {
                ...state,
                addPanel: {
                    ...state.addPanel,
                    boxShadow3: action.boxShadow3,
                }
            };
        case 'CHANGE_ADD_PANEL_BOXSHADOWCOLOR':
            return {
                ...state,
                addPanel: {
                    ...state.addPanel,
                    boxShadowColor: action.boxShadowColor,
                }
            };
        case 'CHANGE_ADD_PANEL_ZINDEX':
            return {
                ...state,
                addPanel: {
                    ...state.addPanel,
                    zIndex: action.zIndex,
                }
            };
        case 'CHANGE_ADD_PANEL_MARGIN':
            return {
                ...state,
                addPanel: {
                    ...state.addPanel,
                    margin: action.margin,
                }
            };
        case 'CHANGE_ADD_PANEL_PADDING':
            return {
                ...state,
                addPanel: {
                    ...state.addPanel,
                    padding: action.padding,
                }
            };
        case 'PANEL_WIDTH_DECREASE':
            return {
                ...state,
                addPanel: {
                    ...state.addPanel,
                    width: parseInt(state.addPanel.width) - 1,
                }
            }
        case 'PANEL_WIDTH_INCREASE':
            return {
                ...state,
                addPanel: {
                    ...state.addPanel,
                    width: parseInt(state.addPanel.width) + 1,
                }
            }
        case 'PANEL_HEIGHT_DECREASE':
            return {
                ...state,
                addPanel: {
                    ...state.addPanel,
                    height: parseInt(state.addPanel.height) - 1,
                }
            }
        case 'PANEL_HEIGHT_INCREASE':
            return {
                ...state,
                addPanel: {
                    ...state.addPanel,
                    height: parseInt(state.addPanel.height) + 1,
                }
            }
        case 'PANEL_MOVE_LEFT_DECREASE':
            return {
                ...state,
                addPanel: {
                    ...state.addPanel,
                    left: Math.round(parseInt(state.addPanel.left) / parseInt(action.offset)) * parseInt(action.offset) - parseInt(action.offset),
                }
            }
        case 'PANEL_MOVE_LEFT_INCREASE':
            return {
                ...state,
                addPanel: {
                    ...state.addPanel,
                    left: Math.round(parseInt(state.addPanel.left) / parseInt(action.offset)) * parseInt(action.offset) + parseInt(action.offset),
                }
            }
        case 'PANEL_MOVE_TOP_DECREASE':
            return {
                ...state,
                addPanel: {
                    ...state.addPanel,
                    top: Math.round(parseInt(state.addPanel.top) / parseInt(action.offset)) * parseInt(action.offset) - parseInt(action.offset),
                }
            }
        case 'PANEL_MOVE_TOP_INCREASE':
            return {
                ...state,
                addPanel: {
                    ...state.addPanel,
                    top: Math.round(parseInt(state.addPanel.top) / parseInt(action.offset)) * parseInt(action.offset) + parseInt(action.offset),
                }
            }
        //edit               
        case 'CHANGE_EDIT_PANEL_LEFT':
            return {
                ...state,
                editPanel: {
                    ...state.editPanel,
                    left: action.left,
                }
            };
        case 'CHANGE_EDIT_PANEL_TOP':
            return {
                ...state,
                editPanel: {
                    ...state.editPanel,
                    top: action.top,
                }
            };
        case 'CHANGE_EDIT_PANEL_WIDTH':
            return {
                ...state,
                editPanel: {
                    ...state.editPanel,
                    width: action.width,
                }
            };
        case 'CHANGE_EDIT_PANEL_HEIGHT':
            return {
                ...state,
                editPanel: {
                    ...state.editPanel,
                    height: action.height,
                }
            };
        case 'CHANGE_EDIT_PANEL_BACKGROUNDCOLOR':
            return {
                ...state,
                editPanel: {
                    ...state.editPanel,
                    backgroundColor: action.color,
                }
            };
        case 'CHANGE_EDIT_PANEL_BORDERRADIUS':
            return {
                ...state,
                editPanel: {
                    ...state.editPanel,
                    borderRadius: action.borderRadius,
                }
            };
        case 'CHANGE_EDIT_PANEL_BOXSHADOW1':
            return {
                ...state,
                editPanel: {
                    ...state.editPanel,
                    boxShadow1: action.boxShadow1,
                }
            };
        case 'CHANGE_EDIT_PANEL_BOXSHADOW2':
            return {
                ...state,
                editPanel: {
                    ...state.editPanel,
                    boxShadow2: action.boxShadow2,
                }
            };
        case 'CHANGE_EDIT_PANEL_BOXSHADOW3':
            return {
                ...state,
                editPanel: {
                    ...state.editPanel,
                    boxShadow3: action.boxShadow3,
                }
            };
        case 'CHANGE_EDIT_PANEL_BOXSHADOWCOLOR':
            return {
                ...state,
                editPanel: {
                    ...state.editPanel,
                    boxShadowColor: action.boxShadowColor,
                }
            };
        case 'CHANGE_EDIT_PANEL_ZINDEX':
            return {
                ...state,
                editPanel: {
                    ...state.editPanel,
                    zIndex: action.zIndex,
                }
            };
        case 'CHANGE_EDIT_PANEL_MARGIN':
            return {
                ...state,
                editPanel: {
                    ...state.editPanel,
                    margin: action.margin,
                }
            };
        case 'CHANGE_EDIT_PANEL_PADDING':
            return {
                ...state,
                editPanel: {
                    ...state.editPanel,
                    padding: action.padding,
                }
            };
        case 'EDIT_PANEL_WIDTH_DECREASE':
            return {
                ...state,
                editPanel: {
                    ...state.editPanel,
                    width: parseInt(state.editPanel.width) - 1,
                }
            }
        case 'EDIT_PANEL_WIDTH_INCREASE':
            return {
                ...state,
                editPanel: {
                    ...state.editPanel,
                    width: parseInt(state.editPanel.width) + 1,
                }
            }
        case 'EDIT_PANEL_HEIGHT_DECREASE':
            return {
                ...state,
                editPanel: {
                    ...state.editPanel,
                    height: parseInt(state.editPanel.height) - 1,
                }
            }
        case 'EDIT_PANEL_HEIGHT_INCREASE':
            return {
                ...state,
                editPanel: {
                    ...state.editPanel,
                    height: parseInt(state.editPanel.height) + 1,
                }
            }
        case 'EDIT_PANEL_MOVE_LEFT_DECREASE':
            return {
                ...state,
                editPanel: {
                    ...state.editPanel,
                    left: Math.round(parseInt(state.editPanel.left) / parseInt(action.offset)) * parseInt(action.offset) - parseInt(action.offset),
                }
            }
        case 'EDIT_PANEL_MOVE_LEFT_INCREASE':
            return {
                ...state,
                editPanel: {
                    ...state.editPanel,
                    left: Math.round(parseInt(state.editPanel.left) / parseInt(action.offset)) * parseInt(action.offset) + parseInt(action.offset),
                }
            }
        case 'EDIT_PANEL_MOVE_TOP_DECREASE':
            return {
                ...state,
                editPanel: {
                    ...state.editPanel,
                    top: Math.round(parseInt(state.editPanel.top) / parseInt(action.offset)) * parseInt(action.offset) - parseInt(action.offset),
                }
            }
        case 'EDIT_PANEL_MOVE_TOP_INCREASE':
            return {
                ...state,
                editPanel: {
                    ...state.editPanel,
                    top: Math.round(parseInt(state.editPanel.top) / parseInt(action.offset)) * parseInt(action.offset) + parseInt(action.offset),
                }
            }
        case 'RESET_PANEL_EDIT_FORM':
            return {
                ...state,
                editPanel: {
                    id: '',
                    width: '',
                    height: '',
                    left: '',
                    top: '',
                    margin: '',
                    backgroundColor: '',
                    padding: '',
                    borderRadius: '',
                    boxShadow1: '',
                    boxShadow2: '',
                    boxShadow3: '',
                    boxShadowColor: '',
                    zIndex: '',
                }
            }
        default:
            return state
    }
}