const initState = {
    editLabel: {
        id: '',
        text: '',
        left: '',
        top: '',
        color: '',
        fontSize: '',
    },
    addLabel: {
        text: 'text',
        left: 200,
        top: 200,
        color: 'black',
        fontSize: 14
    }

}
export const labelReducer = (state = initState, action) => {
    switch (action.type) {
        case 'TOGGLE_LABEL':
            console.log(action.Label)
            return {
                ...state,
                editLabel: {
                    id: action.Label.id,
                    text: action.Label.text,
                    left: action.Label.left,
                    top: action.Label.top,
                    color: action.Label.color,
                    fontSize: action.Label.fontSize,
                }

            };
        case 'CHANGE_ADD_LABEL_LEFT':
            return {
                ...state,
                addLabel: {
                    ...state.addLabel,
                    left: action.left,
                }
            };
        case 'CHANGE_ADD_LABEL_TOP':
            return {
                ...state,
                addLabel: {
                    ...state.addLabel,
                    top: action.top,
                }
            };
        case 'CHANGE_ADD_LABEL_TEXT':
            return {
                ...state,
                addLabel: {
                    ...state.addLabel,
                    text: action.text,
                }
            };
        case 'CHANGE_ADD_LABEL_COLOR':
            return {
                ...state,
                addLabel: {
                    ...state.addLabel,
                    color: action.color,
                }
            };
        case 'CHANGE_ADD_LABEL_FONTSIZE':
            return {
                ...state,
                addLabel: {
                    ...state.addLabel,
                    fontSize: action.fontSize,
                }
            };
        case 'LABEL_MOVE_LEFT_DECREASE':
            return {
                ...state,
                addLabel: {
                    ...state.addLabel,
                    left: Math.round(parseInt(state.addLabel.left) / parseInt(action.offset)) * parseInt(action.offset) - parseInt(action.offset),
                }
            }
        case 'LABEL_MOVE_LEFT_INCREASE':
            return {
                ...state,
                addLabel: {
                    ...state.addLabel,
                    left: Math.round(parseInt(state.addLabel.left) / parseInt(action.offset)) * parseInt(action.offset) + parseInt(action.offset),
                }
            }
        case 'LABEL_MOVE_TOP_DECREASE':
            return {
                ...state,
                addLabel: {
                    ...state.addLabel,
                    top: Math.round(parseInt(state.addLabel.top) / parseInt(action.offset)) * parseInt(action.offset) - parseInt(action.offset),
                }
            }
        case 'LABEL_MOVE_TOP_INCREASE':
            return {
                ...state,
                addLabel: {
                    ...state.addLabel,
                    top: Math.round(parseInt(state.addLabel.top) / parseInt(action.offset)) * parseInt(action.offset) + parseInt(action.offset),
                }
            }
        //edit               
        case 'CHANGE_EDIT_LABEL_LEFT':
            return {
                ...state,
                editLabel: {
                    ...state.editLabel,
                    left: action.left,
                }
            };
        case 'CHANGE_EDIT_LABEL_TOP':
            return {
                ...state,
                editLabel: {
                    ...state.editLabel,
                    top: action.top,
                }
            };
        case 'CHANGE_EDIT_LABEL_TEXT':
            return {
                ...state,
                editLabel: {
                    ...state.editLabel,
                    text: action.text,
                }
            };
        case 'CHANGE_EDIT_LABEL_COLOR':
            return {
                ...state,
                editLabel: {
                    ...state.editLabel,
                    color: action.color,
                }
            };
        case 'CHANGE_EDIT_LABEL_FONTSIZE':
            return {
                ...state,
                editLabel: {
                    ...state.editLabel,
                    fontSize: action.fontSize,
                }
            };
        case 'EDIT_LABEL_MOVE_LEFT_DECREASE':
            return {
                ...state,
                editLabel: {
                    ...state.editLabel,
                    left: Math.round(parseInt(state.editLabel.left) / parseInt(action.offset)) * parseInt(action.offset) - parseInt(action.offset),
                }
            }
        case 'EDIT_LABEL_MOVE_LEFT_INCREASE':
            return {
                ...state,
                editLabel: {
                    ...state.editLabel,
                    left: Math.round(parseInt(state.editLabel.left) / parseInt(action.offset)) * parseInt(action.offset) + parseInt(action.offset),
                }
            }
        case 'EDIT_LABEL_MOVE_TOP_DECREASE':
            return {
                ...state,
                editLabel: {
                    ...state.editLabel,
                    top: Math.round(parseInt(state.editLabel.top) / parseInt(action.offset)) * parseInt(action.offset) - parseInt(action.offset),
                }
            }
        case 'EDIT_LABEL_MOVE_TOP_INCREASE':
            return {
                ...state,
                editLabel: {
                    ...state.editLabel,
                    top: Math.round(parseInt(state.editLabel.top) / parseInt(action.offset)) * parseInt(action.offset) + parseInt(action.offset),
                }
            }
        case 'RESET_LABEL_EDIT_FORM':
            return {
                ...state,
                editLabel: {
                    id: '',
                    text: '',
                    left: '',
                    top: '',
                    color: '',
                    fontSize: '',
                }
            }
        default:
            return state
    }
}