const InitialState = {
    addWordAdjust: {
        top: '100',
        left: '100',
        width: '100',
        height: '30',
        fontSize: '14',
        tag: '1',
        text: '1234'
    },
    editWordAdjust: {
        top: '',
        left: '',
        width: '',
        height: '',
        fontSize: '',
        tag: '',
        text: '',
        id: '',
    },
    clickWordAdjust: {
        top: '',
        left: '',
        width: '',
        height: '',
        fontSize: '',
        tag: '',
        text: '',
        id: '',
    }
};
export const wordAdjustReducer = (state = InitialState, action) => {
    switch (action.type) {
        case 'TOGGLE_WORDADJUST':
            return {
                ...state,
                editWordAdjust: {
                    ...state.editWordAdjust,
                    left: action.wordAdjust.left,
                    top: action.wordAdjust.top,
                    width: action.wordAdjust.width,
                    height: action.wordAdjust.height,
                    fontSize: action.wordAdjust.fontSize,
                    tag: action.wordAdjust.tag,
                    text: action.wordAdjust.text,
                    id: action.wordAdjust.id,
                }
            };
        case 'CLICK_WORDADJUST':
            return {
                ...state,
                clickWordAdjust: {
                    left: action.wordAdjust.left,
                    top: action.wordAdjust.top,
                    width: action.wordAdjust.width,
                    height: action.wordAdjust.height,
                    fontSize: action.wordAdjust.fontSize,
                    tag: action.wordAdjust.tag,
                    text: action.wordAdjust.text,
                    id: action.wordAdjust.id,
                }
            };
        case 'CHANGE_ADD_WORDADJUST_LEFT':
            return {
                ...state,
                addWordAdjust: {
                    ...state.addWordAdjust,
                    left: action.left,
                }
            };
        case 'CHANGE_ADD_WORDADJUST_TOP':
            return {
                ...state,
                addWordAdjust: {
                    ...state.addWordAdjust,
                    top: action.top,
                }
            };
        case 'CHANGE_ADD_WORDADJUST_TAG':
            return {
                ...state,
                addWordAdjust: {
                    ...state.addWordAdjust,
                    tag: action.tag,
                }
            };
        case 'CHANGE_ADD_WORDADJUST_TEXT':
            return {
                ...state,
                addWordAdjust: {
                    ...state.addWordAdjust,
                    text: action.text,
                }
            };
        case 'CHANGE_ADD_WORDADJUST_WIDTH':
            return {
                ...state,
                addWordAdjust: {
                    ...state.addWordAdjust,
                    width: action.width,
                }
            };
        case 'CHANGE_ADD_WORDADJUST_HEIGHT':
            return {
                ...state,
                addWordAdjust: {
                    ...state.addWordAdjust,
                    height: action.height,
                }
            };
        case 'CHANGE_ADD_WORDADJUST_FONTSIZE':
            return {
                ...state,
                addWordAdjust: {
                    ...state.addWordAdjust,
                    fontSize: action.fontSize,
                }
            };
        case 'WORDADJUST_MOVE_LEFT_DECREASE':
            return {
                ...state,
                addWordAdjust: {
                    ...state.addWordAdjust,
                    left: Math.round(parseInt(state.addWordAdjust.left) / parseInt(action.offset)) * parseInt(action.offset) - parseInt(action.offset),
                }
            }
        case 'WORDADJUST_MOVE_LEFT_INCREASE':
            return {
                ...state,
                addWordAdjust: {
                    ...state.addWordAdjust,
                    left: Math.round(parseInt(state.addWordAdjust.left) / parseInt(action.offset)) * parseInt(action.offset) + parseInt(action.offset),
                }
            }
        case 'WORDADJUST_MOVE_TOP_DECREASE':
            return {
                ...state,
                addWordAdjust: {
                    ...state.addWordAdjust,
                    top: Math.round(parseInt(state.addWordAdjust.top) / parseInt(action.offset)) * parseInt(action.offset) - parseInt(action.offset),
                }
            }
        case 'WORDADJUST_MOVE_TOP_INCREASE':
            return {
                ...state,
                addWordAdjust: {
                    ...state.addWordAdjust,
                    top: Math.round(parseInt(state.addWordAdjust.top) / parseInt(action.offset)) * parseInt(action.offset) + parseInt(action.offset),
                }
            }
        //edit
        case 'CHANGE_EDIT_WORDADJUST_LEFT':
            return {
                ...state,
                editWordAdjust: {
                    ...state.editWordAdjust,
                    left: action.left,
                }
            };
        case 'CHANGE_EDIT_WORDADJUST_TOP':
            return {
                ...state,
                editWordAdjust: {
                    ...state.editWordAdjust,
                    top: action.top,
                }
            };
        case 'CHANGE_EDIT_WORDADJUST_TAG':
            return {
                ...state,
                editWordAdjust: {
                    ...state.editWordAdjust,
                    tag: action.tag,
                }
            };
        case 'CHANGE_EDIT_WORDADJUST_TEXT':
            return {
                ...state,
                editWordAdjust: {
                    ...state.editWordAdjust,
                    text: action.text,
                }
            };
        case 'CHANGE_EDIT_WORDADJUST_WIDTH':
            return {
                ...state,
                editWordAdjust: {
                    ...state.editWordAdjust,
                    width: action.width,
                }
            };
        case 'CHANGE_EDIT_WORDADJUST_HEIGHT':
            return {
                ...state,
                editWordAdjust: {
                    ...state.editWordAdjust,
                    height: action.height,
                }
            };
        case 'CHANGE_EDIT_WORDADJUST_FONTSIZE':
            return {
                ...state,
                editWordAdjust: {
                    ...state.editWordAdjust,
                    fontSize: action.fontSize,
                }
            };
        case 'EDIT_WORDADJUST_MOVE_LEFT_DECREASE':
            return {
                ...state,
                editWordAdjust: {
                    ...state.editWordAdjust,
                    left: Math.round(parseInt(state.editWordAdjust.left) / parseInt(action.offset)) * parseInt(action.offset) - parseInt(action.offset),
                }
            }
        case 'EDIT_WORDADJUST_MOVE_LEFT_INCREASE':
            return {
                ...state,
                editWordAdjust: {
                    ...state.editWordAdjust,
                    left: Math.round(parseInt(state.editWordAdjust.left) / parseInt(action.offset)) * parseInt(action.offset) + parseInt(action.offset),
                }
            }
        case 'EDIT_WORDADJUST_MOVE_TOP_DECREASE':
            return {
                ...state,
                editWordAdjust: {
                    ...state.editWordAdjust,
                    top: Math.round(parseInt(state.editWordAdjust.top) / parseInt(action.offset)) * parseInt(action.offset) - parseInt(action.offset),
                }
            }
        case 'EDIT_WORDADJUST_MOVE_TOP_INCREASE':
            return {
                ...state,
                editWordAdjust: {
                    ...state.editWordAdjust,
                    top: Math.round(parseInt(state.editWordAdjust.top) / parseInt(action.offset)) * parseInt(action.offset) + parseInt(action.offset),
                }
            }
        case 'RESET_WORDADJUST_EDIT_FORM':
            return {
                ...state,
                editWordAdjust: {
                    top: '',
                    left: '',
                    width: '',
                    height: '',
                    fontSize: '',
                    tag: '',
                    text: '',
                    id: '',
                },
            }
        default:
            return state;
    }

};