import React, { Component } from 'react';
import { connect } from 'react-redux';
import Button from '@material-ui/core/Button';
import Snackbar from '@material-ui/core/Snackbar';
import { withStyles } from '@material-ui/core/styles';
import MySnackbarContentWrapper from './MySnackbarContentWrapper'
import { actionToggleOn,actionToggleOff } from '../../../redux/actions/snackBarAction'
const Styles = theme => ({
  margin: {
    margin: theme.spacing.unit*(1),
    zIndex: 100,
  },
})
class CustomizedSnackbars extends Component {
  render() {
    const classes = this.props;
    
    return (
      <div>
        <Snackbar
          anchorOrigin={{
            vertical: 'bottom',
            horizontal: 'right',
          }}
          open={this.props.setOpen}
          autoHideDuration={10000}
          onClose={this.props.actionToggleOff}
        >
          <MySnackbarContentWrapper
            onClose={this.props.actionToggleOff}
            variant={this.props.status}
            message={this.props.message}
          />
        </Snackbar>
      </div>
    );
  }
}
const mapStateToProps = (state, ownProps) => {
  return {
    setOpen: state.snackBar.setOpen,
    status:state.snackBar.status,
    message:state.snackBar.message
  }
}
const mapDispatchToProps = (dispatch, ownProps) => {
  return {
    actionToggleOn: () => {
      dispatch(actionToggleOn())
    },
    actionToggleOff: () => {
      dispatch(actionToggleOff())
    }
  }
}
export default connect(mapStateToProps,mapDispatchToProps)(withStyles(Styles)(CustomizedSnackbars));