const InitialState = {
    setOpen: false,
    status:'success' ,// success:'success', error:'error', warning:'warning',info:'info',
    message:''
};
export const snackBarReducer = (state = InitialState, action) => {
    switch (action.type) {
        case 'SNACK_TOGGLE_ON':
            return { 
                ...state, 
                setOpen: true,
                message:action.message,
                status:action.status,
            };
        case 'SNACK_TOGGLE_OFF':
            return { ...state, setOpen: false };
        
        default:
            return state;
    }

};